import { Component, OnInit } from '@angular/core';
import { IManagedObject, InventoryService, IResultList } from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';
import { AlertService, gettext, ModalService, Status } from '@c8y/ngx-components';
import { cloneDeep } from 'lodash-es';
import { BsModalService } from 'ngx-bootstrap';
import { AddDeviceProfileComponent } from './add-device-profile.component';
import { DeviceProfileService } from './device-profile.service';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { tap, switchMap } from 'rxjs/operators';

@Component({
  selector: 'c8y-device-profile-list',
  templateUrl: './device-profile-list.component.html'
})
export class DeviceProfileListComponent implements OnInit {
  reloading: boolean = false;
  reload: BehaviorSubject<void> = new BehaviorSubject(null);
  deviceProfiles$: Observable<IResultList<IManagedObject>> = this.reload.pipe(
    tap(() => (this.reloading = true)),
    switchMap(() => this.deviceProfileService.getDeviceProfiles()),
    tap(() => (this.reloading = false))
  );

  constructor(
    private inventoryService: InventoryService,
    private translateService: TranslateService,
    private modalService: ModalService,
    private alertService: AlertService,
    private bsModalService: BsModalService,
    private router: Router,
    private deviceProfileService: DeviceProfileService
  ) {}

  ngOnInit() {
    this.loadDeviceProfiles();
  }

  loadDeviceProfiles() {
    this.reload.next();
  }

  async createDeviceProfile() {
    const modal = this.bsModalService.show(AddDeviceProfileComponent, {
      class: 'modal-sm'
    }).content as AddDeviceProfileComponent;
    try {
      const profileId = await modal.result;
      modal.close();
      this.router.navigateByUrl(`/device-profiles/${profileId}`);
    } catch (ex) {
      // do nothing
    }
  }

  async duplicateDeviceProfile(deviceProfile) {
    const copy = cloneDeep(deviceProfile);
    copy.id = null;
    copy.name = 'Duplicate of ' + deviceProfile.name;
    const mo = (await this.deviceProfileService.createDeviceProfile(copy)).data;
    this.router.navigateByUrl(`/device-profiles/${mo.id}`);
  }

  async deleteDeviceProfile(deviceProfile) {
    const deviceProfileName = deviceProfile.name;
    const title = gettext('Delete device profile');
    const confirmationText = this.translateService.instant(
      gettext('You are about to delete a device profile "{{ deviceProfileName }}".'),
      { deviceProfileName }
    );
    const finalQuestion = this.translateService.instant(gettext('Do you want to proceed?'));
    try {
      await this.modalService.confirm(
        title,
        `${confirmationText} ${finalQuestion}`,
        Status.DANGER,
        {
          ok: gettext('Delete')
        }
      );
      await this.delete(deviceProfile.id);
      this.reload.next();
    } catch (ex) {
      // do nothing
    }
  }

  private async delete(profileId) {
    try {
      await this.inventoryService.delete(profileId);
      this.alertService.success(gettext('Device profile deleted.'));
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }
}
