import { Component, EventEmitter } from '@angular/core';
import { gettext, ISelectModalObject, ISelectModalOption, ModalLabels } from '@c8y/ngx-components';
import { IManagedObject, QueriesUtil } from '@c8y/client';
import { Subject, Observable } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';
import { RepositoryService, RepositoryType } from '@c8y/ngx-components/repository';
import { assign } from 'lodash-es';

@Component({
  selector: 'c8y-select-configuration-modal',
  templateUrl: './select-configuration-modal.component.html'
})
export class SelectConfigurationModalComponent {
  title: string = gettext('Select configuration');
  load: Subject<void> = new Subject();
  configurations: Observable<any[]> = this.load.pipe(
    switchMap(() => this.getItems()),
    map(({ data }) => this.aggregate(data))
  );
  resultEmitter: EventEmitter<any[]> = new EventEmitter<any>();
  selected: [];
  deviceTypeQuery: any = {};
  searchQuery: any = {};
  labels: ModalLabels = { ok: gettext('Save') };
  private queriesUtil: QueriesUtil;

  constructor(private repositoryService: RepositoryService) {
    this.queriesUtil = new QueriesUtil();
  }

  search(searchTerm) {
    if (!searchTerm) {
      this.searchQuery = {};
    } else {
      this.searchQuery = this.queriesUtil.addOrFilter(
        { name: `*${searchTerm}*` },
        { configurationType: `*${searchTerm}*` }
      );
    }
    this.load.next();
  }

  result(selectedItems) {
    this.resultEmitter.emit(selectedItems);
  }

  getItems() {
    return this.repositoryService.listRepositoryEntries(RepositoryType.CONFIGURATION, {
      query: this.queriesUtil.addOrFilter(this.deviceTypeQuery, this.searchQuery),
      params: { pageSize: 100 }
    });
  }

  aggregate(mos: IManagedObject[]): ISelectModalObject[] {
    const selectedItems: any[] = this.selected;
    return mos.reduce((acc, curr) => {
      curr.configurationType = curr.configurationType || curr.name;
      const selected =
        selectedItems && selectedItems.filter(val => val.url === curr.url).length > 0;
      const selectModalOption: ISelectModalOption = {
        body: [{ value: curr.name }],
        obj: curr,
        selected
      };
      let selectModalObject: ISelectModalObject = acc.find(
        val => val.body[0].value === curr.configurationType
      ) as ISelectModalObject;
      if (selectModalObject) {
        selectModalObject.options.push(selectModalOption);
      } else {
        selectModalObject = {
          groupId: curr.id,
          body: [{ value: curr.configurationType }],
          options: [selectModalOption]
        };
        acc.push(selectModalObject);
      }
      return acc;
    }, []);
  }
}
