import { Component, OnDestroy, OnInit, Pipe } from '@angular/core';
import { IManagedObject, IOperation, IResultList, Realtime } from '@c8y/client';
import { ActivatedRoute } from '@angular/router';
import { pipe, Subscription } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { DeviceProfileService } from '../device-profile.service';
import { DeviceProfile, ProfileItem } from '../device-profile.model';

@Component({
  selector: 'c8y-device-tab-profile',
  templateUrl: './device-tab-profile.component.html'
})
export class DeviceTabProfileComponent implements OnInit, OnDestroy {
  device: IManagedObject;
  deviceProfiles: IResultList<IManagedObject>;
  selectedProfile: Partial<DeviceProfile>;
  operation: IOperation;
  firmwareItems: ProfileItem[] = [];
  softwareItems: ProfileItem[] = [];
  configurationItems: ProfileItem[] = [];

  filterPipe: Pipe;
  pattern: string = '';
  reloading: boolean = false;
  private operationsSubscription: Subscription;

  constructor(
    private deviceProfileService: DeviceProfileService,
    private route: ActivatedRoute,
    private realtime: Realtime
  ) {}

  async ngOnInit() {
    this.device = this.route.snapshot.parent.data.contextData;
    this.getDeviceProfilesAndUpdateProfileItems();
  }

  async getDeviceProfilesAndUpdateProfileItems() {
    this.reloading = true;
    this.deviceProfiles = await this.deviceProfileService.getDeviceProfilesByDeviceType(
      this.device.type
    );
    if (this.device.c8y_Profile) {
      const profileId = this.device.c8y_Profile.profileId;
      this.selectedProfile = this.deviceProfiles.data.find(mo => mo.id === profileId);
    }
    this.updateProfileItems(this.device, this.selectedProfile);
    this.operation = await this.deviceProfileService.getProfileOperation(this.device.id);
    this.subscribeToOperations();
    this.reloading = false;
  }

  selectProfile(mo: DeviceProfile) {
    this.selectedProfile = mo;
    this.updateProfileItems(this.device, this.selectedProfile);
  }

  async createOperation() {
    this.operation = await this.deviceProfileService.createProfileOperation(
      this.device,
      this.selectedProfile
    );
  }

  setPipe(filterStr: string) {
    this.pattern = filterStr;
    this.filterPipe = pipe(
      map((data: any[]) => {
        return data.filter(
          (mo: any) => mo.name && mo.name.toLowerCase().indexOf(filterStr.toLowerCase()) > -1
        );
      })
    );
  }

  ngOnDestroy() {
    this.operationsSubscription.unsubscribe();
  }

  private updateProfileItems(device, profile) {
    this.firmwareItems = this.deviceProfileService.getFirmwareItems(device, profile);
    this.softwareItems = this.deviceProfileService.getSoftwareItems(device, profile);
    this.configurationItems = this.deviceProfileService.getConfigurationItems(device, profile);
  }

  private subscribeToOperations() {
    const operationsChannel = `/operations/${this.device.id}`;
    this.operationsSubscription = this.realtime
      .observable(operationsChannel)
      .pipe(filter(({ data }) => data.c8y_DeviceProfile))
      .subscribe(({ data }) => {
        this.operation = data;
      });
  }
}
