import { Component } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { DeviceProfile } from './device-profile.model';
import { DeviceProfileService } from './device-profile.service';
import { gettext } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-add-device-profile',
  templateUrl: './add-device-profile-component.html'
})
export class AddDeviceProfileComponent {
  DEVICE_TYPE_POPOVER = gettext('The device profile will be available for assignments on devices of the specified type. Otherwise, it will be available for all device types.');
  deviceProfile: Partial<DeviceProfile> = {
    name: '',
    type: 'c8y_Profile',
    c8y_Filter: {},
    c8y_DeviceProfile : {
    }
  };
  result: Promise<any> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });
  private _save;
  private _cancel;

  constructor(private modal: BsModalRef, private deviceProfileService: DeviceProfileService) {}

  async create() {
    const mo = (await this.deviceProfileService.createDeviceProfile(this.deviceProfile)).data;
    this._save(mo.id);
  }

  close() {
    this._cancel();
    this.modal.hide();
  }
}
