import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';

@Injectable()
export class DeviceProfileNavigationFactory implements NavigatorNodeFactory {
  nodeItem: NavigatorNode;

  async get() {
    if (!this.nodeItem) {
      this.nodeItem = new NavigatorNode({
        label: gettext('Device profiles'),
        path: '/device-profiles',
        icon: 'c8y-device-profile',
        parent: gettext('Management')
      });
    }
    return this.nodeItem;
  }
}
